import Navbar from './Navbar'
import React, { useState, useEffect } from 'react';
import {Box, Tab, Tabs, Stack, Pagination, Typography, Paper} from '@mui/material';
import useFetch3 from "../Requester/useFetch3";
import useFetch4 from "../Requester/useFetch4";
import Post from './Post'
import domain from '../Content/domain.json'
import Footer from '../Content/Footer';
import background from '../Media/background2.jpg';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import Link from '@mui/material/Link';
import logo from '../Media/logo.jpg';


const Inspiration = (props) => {
  const {data3, setData3} = useFetch3();
  const {data4, setData4} = useFetch4();
  const [value, setValue] = useState('planning');
  const [page, setPage] = useState(1);
  console.log(data4.results);
  const theme = createTheme({
    typography: {
      allVariants: {
        fontFamily: 'Calibri',
        textTransform: 'none',
        fontSize: 18,
      },
    },
  });

  const handleChange = (event, valueR) => {
    setPage(valueR);
  };

  useEffect(() => {
      let m = domain.domain+"/posts/api";
            setData3({ ...data3, slug: m });   
      let n = domain.domain+"/parse/courses/read/literature";
            setData4({ ...data4, slug: n });     
  setTimeout(()=>{
      }, 100)
    
    }, [])


    const [orientation, setOrientation] = useState('vertical');

    useEffect(() => {
      const handleResize = () => {
        if (window.innerWidth < 1600 && window.innerWidth > 1500) {
          setOrientation('vertical');
        } else if(window.innerWidth < 1400) {
          setOrientation('vertical');
        }
      };
      handleResize(); // Set initial orientation based on screen width
  
      window.addEventListener('resize', handleResize);
  
      return () => {
        window.removeEventListener('resize', handleResize);
      };
    }, []);


if(value != "literature"){
return(
<div style={{ backgroundImage:`url(${background})`, backgroundRepeat:"repeat"}}>
          <Link to="/" sx={{width: '100px', '@media(min-width: 1085px)': { display: 'none'}}}>
    <img style={{ width: '200px', display: 'block', margin: 'auto', marginBottom: '5px',}} src={logo} alt="Pedagogoium logo"
      onClick={() => (window.location.href = domain.domain + `/`)}/>
  </Link>
  <Navbar/><br></br> <ThemeProvider theme={theme}>


  <Box sx={{marginTop: '25px', position: 'absolute', '@media(max-width: 1350)': { marginLeft: '25%', position: 'relative' }, '@media(max-width: 1500px)': { position: 'relative', marginLeft: '30%'}, '@media(max-width: 850px)': { position: 'relative', marginLeft: '28%'}, '@media(max-width: 600px)': { position: 'relative', marginLeft: '10%'}, '@media(max-width: 365)': { position: 'relative', marginLeft: '1%'} }} >
      <Tabs
        orientation={orientation}
        aria-label="
return(Vertical tabs example"
        sx={{ borderRight: 1, borderColor: 'divider' }}>
        <Tab label="Plánování a příprava výuky"  onClick={(e) => (setValue('planning'), setPage(1))} />
        <Tab label="Vedení výuky" onClick={(e) => (setValue('leadership'), setPage(1))}/>
        <Tab label="Během hodiny"  onClick={(e) => (setValue('during'), setPage(1))} />
        <Tab label="Technologie ve výuce" onClick={(e) => (setValue('technology'), setPage(1))}/>
        <Tab label="Zpětná vazba a hodnocení"  onClick={(e) => (setValue('feedback'), setPage(1))}/>
        <Tab label="Kolegiální podpora" onClick={(e) => (setValue('support'), setPage(1))}/>
        <Tab label="Reflexe výuky a profesní rozvoj" onClick={(e) => (setValue('reflection'), setPage(1))}/>
        <Tab label="Literatura" onClick={(e) => (setValue('literature'))}/>
      </Tabs>
    </Box>
    <Box
      sx={{
        width: '65%',
        margin: '0 auto',
        '@media(min-width: 1200px)': {
          marginTop: '25px'
        },
        '@media(max-width: 1200px)': {
          width: '85%'
        },
      }}
    >
  {!data3.results ? "Loading..." : 
              data3.results.reverse().filter(function(d){
                return d.section.includes(value)}).slice((page-1)*10,(page-1)*10+10).map(({ _id, caption, contentShort, content, url, section, date, imageURL}) =>
          <Post caption={caption} contentShort={contentShort} content={content} imageURL={imageURL}  url={url} key={_id} _id={_id}></Post>
            )}</Box>
  <div style={{width: '10%'}}></div>

<div style={{display: 'block', width: 'fit-content', margin: 'auto'}}><br></br>    <Pagination  count={Math.ceil((data3.results.filter(function(d){
  return d.section.includes(value)}).length)/10)} page={page} onChange={handleChange} sx={{paddingBottom: '120px'}}/></div>
<Footer></Footer></ThemeProvider></div>)}
else{
  return(
    <div style={{ backgroundImage:`url(${background})`, backgroundRepeat:"repeat"}}>
  <Navbar/><br></br> <ThemeProvider theme={theme}>
  <Box sx={{marginTop: '25px', position: 'absolute', '@media(max-width: 1350)': { marginLeft: '25%', position: 'relative' }, '@media(max-width: 1220px)': { position: 'relative', marginLeft: '30%'}, '@media(max-width: 850px)': { position: 'relative', marginLeft: '28%'}, '@media(max-width: 600px)': { position: 'relative', marginLeft: '10%'}, '@media(max-width: 365)': { position: 'relative', marginLeft: '1%'} }} >
      <Tabs
        orientation={orientation}
        aria-label="return(Vertical tabs example"
        sx={{ borderRight: 1, borderColor: 'divider' }}>
        <Tab label="Plánování a příprava výuky"  onClick={(e) => (setValue('planning'), setPage(1))} />
        <Tab label="Vedení výuky" onClick={(e) => (setValue('leadership'), setPage(1))}/>
        <Tab label="Technologie ve výuce" onClick={(e) => (setValue('technology'), setPage(1))}/>
        <Tab label="Zpětná vazba a hodnocení"  onClick={(e) => (setValue('feedback'), setPage(1))}/>
        <Tab label="Kolegiální podpora" onClick={(e) => (setValue('support'), setPage(1))}/>
        <Tab label="Reflexe výuky a profesní rozvoj" onClick={(e) => (setValue('reflection'), setPage(1))}/>
        <Tab label="Literatura" onClick={(e) => (setValue('literature'))}/>
      </Tabs>
    </Box>
    <Paper sx={{width: '55%', display: 'block', margin: 'auto', marginTop: '25px', padding: '35px', '@media(max-width: 920px)': {marginTop: '300px', width: '85%'}}}>
<div dangerouslySetInnerHTML={{ __html: data4.results }}></div>
</Paper>
<div style={{display: 'block', width: 'fit-content', margin: 'auto'}}><br></br><Pagination  count={Math.ceil((data3.results.filter(function(d){
  return d.section.includes(value)}).length)/10)} page={page} onChange={handleChange} sx={{paddingBottom: '120px'}}/></div>
<Footer></Footer></ThemeProvider></div>)}
}
export default Inspiration;