import  React, { useState, useEffect } from 'react';
import {Stack, AppBar, Container, Paper, Typography, Link, Box} from '@mui/material';
import FacebookIcon from '@mui/icons-material/Facebook';
import WebIcon from '@mui/icons-material/Web';
import {BottomNavigationAction, BottomNavigation} from '@mui/material';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import logo from '../Media/logoUK.png';

const Footer = () => {    
    const theme = createTheme({
        typography: {
          allVariants: {
            fontFamily: 'Calibri',
            textTransform: 'none',
            fontSize: 18,
            '@media(max-width: 600px)': { fontSize: '5pt' }
          },
        },
      });  
      const [showElement, setShowElement] = useState(false);

      useEffect(() => {
        const timeoutId = setTimeout(() => {
          setShowElement(true);
        }, 1000); // Delay in milliseconds (3 seconds in this example)
    
        return () => clearTimeout(timeoutId); // Clear the timeout if the component unmounts before the delay ends
      }, []);

return(


  

    <div> <ThemeProvider theme={theme}>  {showElement &&   <Paper sx={{paddingTop: '25px', backgroundColor: 'white', marginTop: '50px', paddingBottom: '25px' }}>
    <Stack direction={"row"} sx={{fontFamily: 'Calibri'}}>    
    <Box sx={{width: '40%'}} >
    <BottomNavigationAction label="CCŽV" icon={<WebIcon />} href="https://cczv.cuni.cz/CCZV-1.html"/>
    <Link sx={{width: '800px', fontSize:'small', fontSize: '10pt', '@media(max-width: 900px)': { display: 'none' }}} href="https://cczv.cuni.cz/CCZV-1.html">CENTRUM CELOŽIVOTNÍHO VZDĚLÁVÁNÍ UK</Link><br></br>
        <BottomNavigationAction label="Fcb" icon={<FacebookIcon />} href="https://www.facebook.com/profile.php?id=100063670832237" />
    <Link href="https://www.facebook.com/profile.php?id=100063670832237" sx={{fontSize: '10pt', '@media(max-width: 900px)': { display: 'none' }}}> FACEBOOK CENTRA CELOŽIVOTNÍHO VZDĚLÁVÁNÍ UK</Link>
        </Box>

        <Box href="/" sx={{'@media(max-width: 1300px)': { width: '200px'}, width: '450px', marginLeft: '8%', marginBottom: '25px', '@media(max-width: 1085px)': { display: 'none'}}}>
              <img src={logo} alt="UK logo"/></Box>

<Typography variant="p" sx={{width: '20%', marginLeft: '10%', marginRight: '10px', fontSize: '12pt', '@media(max-width: 1000px)': { fontSize: '8pt', marginLeft: '20%' }, '@media(max-width: 500px)': { marginLeft: '35%', fontSize: '5pt' }, '@media(max-width: 400px)': {marginLeft: '25%' }}}><strong>Mgr. et Mgr. David Hurný</strong><br></br>
    Předseda Paedagogia<br></br>
    Člen kolegia rektorky pro rozvoj pedagogických kompetencí akademických pracovníků<br></br>
    e-mail: <a href="mailto:david.hurny@ruk.cuni.cz">david.hurny@ruk.cuni.cz</a>
</Typography>

<Typography variant="p" sx={{width: '20%', marginRight: '20px',fontSize: '12pt', '@media(max-width: 1000px)': { fontSize: '8pt', marginRight: '100px' }, '@media(max-width: 500px)': {fontSize: '5pt' }}}>
    <strong>Mgr. Bartłomiej Wróblewski</strong><br></br>
    Centrum celoživotního vzdělávání<br></br>
    Koordinátor rozvoje pedagogických kompetencí<br></br>
    e-mail: <Link href="mailto:bartlomiej.wroblewski@ruk.cuni.cz">bartlomiej.wroblewski@ruk.cuni.cz</Link><br></br>
    mobil: +420 771 277 692
</Typography>
</Stack>
    </Paper>}
 
    </ThemeProvider>

    </div>

);
}

export default Footer;;